import { useState } from "react";

const fragmentWidth = 200;
const headerHeight = 40;
// calculate like in css file
const previewFragmentHeight = fragmentWidth * 0.7;

const useWrapper = (viewModeNames) => {
  const [viewMode, setViewMode] = useState(viewModeNames.default);
  const [anchorPosition, setAnchorPosition] = useState({ top: 0, left: 0 });
  const [transformOrigin, setTransformOrigin] = useState({
    vertical: "top",
    horizontal: "left",
  });

  const mouseEnterHandler = (event) => {
    const { clientX, clientY } = event;
    const { scrollX, innerWidth } = window;
    const [top, vertical] =
      clientY < previewFragmentHeight + headerHeight
        ? [clientY + 2, "top"]
        : [clientY - 2, "bottom"];
    const [left, horizontal] =
      clientX + fragmentWidth > innerWidth
        ? [innerWidth, "right"]
        : [clientX, "left"];
    setAnchorPosition({
      // left: Math.max(
      //   0,
      //   Math.min(
      //     clientX + scrollX - halfFragmentWidth,
      //     innerWidth - fragmentWidth
      //   )
      // ),
      left,
      top,
    });
    setTransformOrigin({ horizontal, vertical });
    setViewMode(viewModeNames.preview);
  };

  const mouseLeaveHandler = (event) => {
    viewMode !== viewModeNames.fullview && setViewMode(viewModeNames.default);
  };

  const clickHandler = (event) => {
    setViewMode(viewModeNames.fullview);
  };

  const fullviewClickHandler = (event) => {
    setViewMode(viewModeNames.default);
  };

  return {
    mouseEnterHandler,
    mouseLeaveHandler,
    clickHandler,
    fullviewClickHandler,
    viewMode,
    anchorPosition,
    transformOrigin,
  };
};

export default useWrapper;
